body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  background: #303c71;
}

.discover-body p {
 width: 70%;
 color: white;
 text-align: left;
 font-size: 1em;
 vertical-align: bottom;
}

.discover-body p,.discover-body h1{
  margin-left:  30px;
  margin-bottom: 20px;
}


#home-button{
  margin: 10px;
  height: 50px;
  width: 50px;
}

.cards-container{
  width: 100%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.card{
  display: -webkit-flex;
  display: flex;
  width: 45%;
  height: 150px;
  margin: 6px;
  border-radius: 25px;
  border: none;
  /* background-color: #FF847C */
}

.card h2 {
  font-size: 1.25rem;
  padding: 1em;
  border-radius: 25px 25px 25px 0;
  /*background-color: rgba(0,0,0,.75) */
  

}


h2,a,a:hover{
  color: white;
  text-decoration: none; 
}

.cards-container::after{
  content: "";
  -webkit-flex: auto;
          flex: auto;
  margin-left: 100px;
}

.story-toolbar{
  text-align: left;
  height: 75px;
  background-color: black;
}


.toolbar{
  font-size: 100%;
  text-align: left;
  height: 75px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items:center;
          align-items:center;
}

#toolbar-icon{
  height: 75%;
}

.discover-body h1{
  color: white;
  margin-top: 10px
}

.question-body{
  width: 100vw;
  height: 100vh;
  background-color: bisque;
}

.question-body h1, .question-body h2{
  text-align: center;
}

.question-body h2{
  color: black;
}

.question-body div{
  background-color: gray;
  color: white;
  font-size: 2em;
  width: 50%;
  margin: 1em auto;
  padding: .5em;
  border-radius: 25px;

}

.discuss-card{
  background-color: lightblue;
  width: 80%;
  height: 500px;
  margin: auto;
  margin-top: 1em;
  border-radius: 10px;
  padding: 1em;
}

.discuss-prompt{
  font-size: 1.5em;
  font-weight: bold;
}

.discuss-card textarea{
  margin-top: 50px;
  width: 90%;
  height:  200px;
  padding: 1em;
  border-radius: 10px;
  resize: none;

}

.discuss-card button{
  background-color: white;
  font-size: 1.5em;
  border: none;
  height: 50px;
  width: 100px;
  border-radius: 10px;
  border: 2px solid black;
}
#landingBody {
    /* The image used */
    background-image: url(/static/media/landguy.8839c948.jpg); 
    height: 100vh;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    overflow: hidden
}

#landingBody img {
    position: relative;
    left: 50px;
    top: 125px;
    width: 70px;
    background-position: center;
}

#landingBody h1 {
    position: relative;
    left: 135px;
    top: 30px;
}

#landingBody h6 {
    position: relative;
    left: 135px;
    top: 20px;
}

#landingBody #future {
    position: relative;
    left: 135px;
    top: 10px;
}

.dot {
